/* parallax */
.parallax {
  -webkit-perspective: 1px;
          perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}

.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* Base */
.parallax__layer--base {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
   background-image: -webkit-gradient(linear,
  left top, right top,
  color-stop(20%, #20123a),
  color-stop(40%, #281346),
  color-stop(60%, #271a4c),
  color-stop(80%, #2f1859)) !important;
   background-image: linear-gradient(90deg,
  #20123a 20%,
  #281346 40%,
  #271a4c 60%,
  #2f1859 80%) !important;
  color: white;
}

.parallax__layer--base .title {
color: white;
text-align: center;
}

.parallax__layer--base .subtitle {
color: white;
text-align: center;
}


/* back */
.parallax__layer--back {
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
  background-color: #031425;
  background: url(/static/media/stars-bg.691060a5.svg) repeat; 
  /* background-size: contain;  */
  opacity: .25;
}
.parallax__layer--back .title {
color: yellow;
}

/* deep */
.parallax__layer--deep {
  -webkit-transform: translateZ(-2px) scale(3);
          transform: translateZ(-2px) scale(3);
  background: url(/static/media/starry.50edafbd.svg) repeat; 
  opacity: .6;
}

.parallax__layer--deep .title {
/* color: red; */
}

.parallax__group {
  position: relative;
  height: 100vh;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

